import axios from 'axios';
import qs from 'qs';
import store from '../store'; //loading
import router from '../router'; //
import { baseUrl } from './env'; //

// 
// 
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;


// axiosjsonformdata

//  POST 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

// ( 'PUT', 'POST'  'PATCH' )
axios.defaults.transformRequest = [(data) => { return qs.stringify(data) }]


//  then/catch 
// axios.defaults.transformResponse = [(data) => { return JSON.parse(data) }]

//  CORS 
// 
axios.defaults.withCredentials = true;

axios.defaults.crossDomain = true;

// 
axios.defaults.timeout = 40000;


// 
// 1interceptor
// 2axiosinterceptor axios axios.create() axios

// axios
let instance = axios.create({
    baseURL: baseUrl,
    headers: { 'Content-Type': 'application/json' },
    timeout: 10000,
    withCredentials: true,
    // transformRequest: [(data) => {//axios
    //     return JSON.stringify(data)
    // }]
})


// Add a request interceptor
instance.interceptors.request.use(function(config) {
    // Do something before request is sent
    store.dispatch( //loading
            'controlLoading', {
                status: true,
                theme: 'dark',
                size: '',
                text: '...'
            })
        // POST  axios post         
    if (config.method === 'post' || config.method === "put" || config.method === "delete") {
        config.data = JSON.stringify(config.data);
    }
    // token , token
    // if (localStorage.AUTH_TOKEN) {
    //     config.headers.Authorization = localStorage.AUTH_TOKEN;
    // }
    if (!localStorage.login) { //
        // https://router.vuejs.org/zh-cn/essentials/navigation.html
        // router.replace({ path: '/error' })
    }
    return config;
}, function(error) {
    // Do something with request error
    store.dispatch(
        'controlLoading', {
            status: false,
        })
    return Promise.reject(error);
});

// Add a response interceptor
instance.interceptors.response.use(function(response) {
    // Do something with response data
    store.dispatch( //loading
        'controlLoading', {
            status: false,
        })
    return response.data;
}, function(error) {
    // Do something with response error
    store.dispatch(
        'controlLoading', {
            status: false,
        })
    return Promise.reject(error);
});



export { instance };


